<template>
  <div class="music" @click="to()">
    <div class="left">
      <div class="imgs">
        <img :style="trans" class="img" v-lazy="'http://127.0.0.1:3000'+res.simg" alt />
      </div>
      <span style="margin-left:1rem;">{{res.song}}</span>
    </div>
    <div class="right_top">
      <div class="right right_bo">
        <img style="width:80%;" src="../assets/icon/bofang.png" alt />
      </div>
      <div class="right">
        <img style="width:100%;" src="../assets/icon/guanli.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
import {Toast} from 'vant'
export default {
  data() {
    return {
      trans: "animation-play-state:paused",
      lid: "",
      res: []
    };
  },
  methods: {
    get() {
      var lid = window.sessionStorage.getItem("lid");
      this.lid = lid;
      let url = "/musiclist";
      var params = { id: lid };
      this.axios.get(url, { params }).then(result => {
        this.res = result.data[0];
        // console.log(this.res);
      });
    },
    to() {
      let lid = this.lid;
      if (lid == null) {
        Toast.fail('您尚未登录，即将跳转登录界面！');
        setTimeout(() => {
          this.$router.push("/login");
        },3000);
        
      } else {
        this.$router.push("/player/" + lid);
      }
    }
  },
  created() {
    this.get();
  }
};
</script>

<style scoped>
img[lazy=loading] {
  width: 100%;
  height: 100px;
  margin: auto;
}
.music {
  width: 100%;
  position: fixed;
  bottom: 3rem;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  z-index: 999;
}
.right_top {
  margin-top: 0.7rem;
  display: flex;
  justify-content: center;
}
.left {
  display: flex;
  align-items: center;
}
.right {
  width: 2rem;
  height: 2rem;
  margin-right: 0.8rem;
}
.right_bo > img {
  transform: translate(2px, 3px);
}
.right_bo {
  border-radius: 50%;
  border: 1px solid #0f0;
}
.imgs {
  width: 3rem;
}
.img {
  width: 100%;
  border-radius: 50%;
  animation: a 15s linear infinite;
}
@keyframes a {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  100% {
    transform: translate(0, 0) rotate(360deg);
  }
}
</style>
